import React, { Component } from "react";
import Datepicker from "./components/Datepicker";
import { Button } from "reactstrap";

class App extends Component {
  constructor() {
    super();
    var today = new Date(),
      date = today.getFullYear() + '/' + (today.getMonth() + 1) + '/' + (today.getDate());
    this.state = {
      date: date
    };
  }

  // handledChange = (event) => {
  //   this.setState({ date: event.target.value })
  //   console.log(event)
  // }

  render() {
    return (
      <div className="w-25 py-5 my-5 mx-auto" style={{ minWidth: "60%" }}>
        <Datepicker label="Birthday" value={this.state.date} />
        {/* <Datepicker label="Birthday" value={this.state.date} onChange={this.handledChange} /> */}
        <button type="button" class="btn btn-primary btn-sm float-right">Small button</button>
      </div>
    );
  }
}

export default App;



